@import url('https://fonts.googleapis.com/css?family=Roboto:400,700');

body {
  margin: 0;
  padding: 10px;
  font-family: 'Roboto', sans-serif;
  min-height: 100vh;
  background-color: rgba(0,0,0,.2);
}

.form-group {
  display: inline-block;
  margin-right: 15px;
  width: 500px;
}
.form-group label {
  margin-bottom: 10px;
  margin-top: 20px;
}
.form-group input {
  padding: 4px 10px;
  box-sizing: border-box;
}
.control {
  background-color: #fff;
  padding: 10px;
  margin-bottom: 10px;
}
.console-wrap {
  max-height: 500px;
  overflow-y: scroll;
  padding: 10px;
  background-color: #fff;
  margin-top: 10px;
}
.form-group label, .form-group input {
  display: block;
  width: 100%;
}
.console-wrap {
  padding: 10px;
}

.trading {
  display: inline-block;
  width: 240px;
  vertical-align: top;
}

.instruments {
  padding: 5px 5px 5px 0;
}

.active {
  background-color: green;
  color: #fff;
}

.instruments button {
  margin: 5px;
  border: 0;
  outline: none;
  padding: 5px 0;
  width: 80px;
  cursor: pointer;
}

input {
  font-size: 16px;
}

.instruments button.active {
  background-color: green;
  color: #fff;
}

.instruments button:first-child {
  margin-left: 0;
}

.console {
  width: 40%;
  padding: 10px;
  font-size: 14px;
}

.console p {
  margin: 0;
}
.trading_wrap {
  display: flex;
}
.tr_item {
  width: 50%;
  box-sizing: border-box;
  display: inline-block;
  padding: 10px;
}

.tr_item h2 {
  margin: 0;
  padding-bottom: 10px;
}

.orders {
  background-color: rgba(0,0,0,0.8);
  color: #fff;
}

.stress {
  margin: 10px 0;
}

.stress input {
  margin-left: 15px;
  width: 100px;
}